@import url(https://fonts.googleapis.com/css?family=Fira+Sans:400,500,600,700,800);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #eeeef0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  -webkit-appearance: none;
}

*:focus {
  outline: none;
}
.conversation-search {
  padding: 10px;
  display: flex;
  flex-direction: column;
  position: relative;
}

.search-icon {
  position: absolute;
  left: 0;
  top: 8px;
  color: #8a8a5c;
  padding-left: 20px;
  padding-top: 7px;
}

.conversation-search-input {
  background: #f4f4f8;
  padding: 8px;
  padding-left: 40px;
  border-radius: 40px;
  border: none;
  font-size: 14px;
}

.conversation-search-input::-webkit-input-placeholder {
  text-align: left;
}

.conversation-search-input::-ms-input-placeholder {
  text-align: left;
}

.conversation-search-input::placeholder {
  text-align: left;
}

.conversation-search-input:focus::-webkit-input-placeholder {
  text-align: left;
}

.conversation-search-input:focus::-ms-input-placeholder {
  text-align: left;
}

.conversation-search-input:focus::placeholder {
  text-align: left;
}

input[type="search"]::-webkit-search-cancel-button {

  /* Remove default */
  -webkit-appearance: none;
  cursor: pointer;

  /* Now your own custom styles */
  height: 10px;
  width: 10px;
  background-image: url(/static/media/Cross.f11b103c.svg);
}

.conversation-list-item:hover {
  background: #eeeef1;
  cursor: pointer;
}
.conversation-list-item {
  display: flex;
  align-items: center;
  padding: 10px;
}
.conversation-photo {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 10px;
}

.conversation-title {
  font-size: 14px;
  font-weight: bold;
  text-transform: capitalize;
  margin: 0;
}

.conversation-snippet {
  font-size: 14px;
  color: #888;
  margin: 0;
}

.toolbar {
  grid-area: toolbar;
  display: flex;
  align-items: center;

  background-color: white;
  font-weight: 500;
  border-bottom: 1px solid #eeeef1;

  position: -webkit-sticky;

  position: sticky;
  top: 0px;
}

@supports {
  .toolbar {
    border: none;
    background-color: rgba(255, 255, 255, 0.8);
    -webkit-backdrop-filter: blur(20px);
            backdrop-filter: blur(20px);
  }
}

.toolbar-title {
  margin: 0;
  font-size: 16px;
  font-weight: 800;
}

.left-items, .right-items {
  flex: 1 1;
  padding: 10px;
  display: flex;
}

.right-items {
  flex-direction: row-reverse;
}

.left-items .toolbar-button {
  margin-right: 20px;
}

.right-items .toolbar-button {
  margin-left: 20px;
}

.left-items .toolbar-button:last-child,
.right-items .toolbar-button:last-child {
  margin: 0;
}

.toolbar-button {
  color: #ffa31a;
  font-size: 28px;
  -webkit-transition: all 0.1s;
  transition: all 0.1s;
}

.toolbar-button:hover {
  cursor: pointer;
  color: #e68a00;
}

.toolbar-button:active {
  color: #ffa31a;
  opacity: 0.25;
}

.conversation-list {
  display: flex;
  flex-direction: column;
}
.emojiPicker {
  position: absolute;
  bottom: 64px;
  right: 0px;
  float: right;
  margin-left: 200px;
}

.getEmojiButton {
  float: right;
  border: none;
  margin: 0;
  cursor: pointer;
}

.compose {
  grid-area: compose;
  padding: 10px;
  display: flex;
  align-items: center;
  background: white;
  border-top: 1px solid #eeeef1;
  position: -webkit-sticky;
  position: sticky;
  width: 100%;
  bottom: 0px;
}

@supports {
  .compose {
    border: none;
    background-color: rgba(255, 255, 255, 0.8);
  }
}

.compose-input {
  flex: 1 1;
  border: none;
  font-size: 14px;
  height: 40px;
  width: 100%;
  background: none;
  padding: 10px;
  margin-left: 20px;
  border-radius: 40px;
  background-color: #d9d9d9;
}

.options {
  background-color: inherit;
  border: none;
  padding: none;
}

.compose-input::-webkit-input-placeholder {
  opacity: 1;
}

.compose-input::-ms-input-placeholder {
  opacity: 1;
}

.compose-input::placeholder {
  opacity: 1;
}

.compose .toolbar-button {
  color: #8a8a5c;
  margin-left: 15px;
}

.compose .toolbar-button:hover {
  color: #4d4d33;
}

.compose .toolbar-button:hover {
  color: #99999c;
}

.message {
  display: flex;
  flex-direction: column;
}

.message .timestamp {
  background-color: white;
  border-radius: 40px;
  display: inline-block;
  justify-content: center;
  color: black;
  font-weight: 600;
  font-size: 12px;
  margin: 10px 0px;
  text-transform: uppercase;
  padding-left: 5px;
  padding-right: 5px;
}

.message .blockcenter {
  text-align: center;
}

.message .bubble-container {
  font-size: 14px;
  display: flex;
}

.message.mine .bubble-container {
  justify-content: flex-end;
}

.message.start .bubble-container .bubble {
  /* margin-top: 10px; */
  border-top-left-radius: 20px;
}

.message.end .bubble-container .bubble {
  border-bottom-left-radius: 20px;
  /* margin-bottom: 10px; */
}

.message.mine.start .bubble-container .bubble {
  margin-top: 10px;
  border-top-right-radius: 20px;
}

.message.mine.end .bubble-container .bubble {
  border-bottom-right-radius: 20px;
  margin-bottom: 10px;
}

.message .bubble-container .bubble {
  margin: 1px 0px;
  background: #ffd699;
  padding: 10px 15px;
  border-radius: 20px;
  max-width: 60%;
  border-top-left-radius: 2px;
  border-bottom-left-radius: 2px;
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
}

.message.mine .bubble-container .bubble {
  background: #ffa31a;
  color: white;
  border-top-left-radius: 20px;
  border-bottom-left-radius: 20px;
  border-top-right-radius: 2px;
  border-bottom-right-radius: 2px;
}

.message-list {
  display: grid;
  grid:
    "toolbar" auto
    "message-list-container" 1fr
    "compose" auto;
  height: 100%;
}

.message-list-container {
  grid-area: message-list-container;
  background-image: url(/static/media/back.73adc9ff.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  padding: 10px;
  padding-bottom: 70px;
}

.conversation-list-item {
  display: flex;
  align-items: center;
  padding: 10px;
}

.conversation-photo {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 10px;
}

.messenger {
  display: grid;
  width: 100%;
  height: 100vh;
  background-image: url(/static/media/background.eae63c0a.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;

  padding-left: 3vw;
  padding-right: 3vw;
  padding-top: 5vh;
  padding-bottom: 5vh;

  grid-template-columns: 350px auto;
  grid-template-rows: 60px auto 60px;
}

.container {
  padding: 10px;
}

.scrollable {
  position: relative;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.sidebar {
  background: white;
  grid-row-start: 1;
  grid-row-end: span 3;
}

.content {
  background: white;
  grid-row-start: 1;
  grid-row-end: span 3;
}

.footer {
  grid-column-start: 2;
  background: white;
}

@media (max-width: 800px) {
  .messenger {
    grid-template-columns: 90vw auto;
    padding-right: 0;
    padding-left: 0;
    justify-content: center;
  }
}

.btn-warning {
  color: #000000;
  background-color: #ffa31a !important;
  border: #ffa31a !important; }

.btn-warning:hover {
  color: #000000;
  background-color: #e68a00 !important;
  border: #e68a00 !important; }

* {
  box-sizing: border-box; }

.auth {
  background-image: url(/static/media/background.662ccd92.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  min-height: 100vh;
  display: flex;
  font-weight: 400;
  font-family: "Fira Sans", sans-serif; }

h1,
h2,
h3,
h4,
h5,
h6,
label,
span {
  font-weight: 500;
  font-family: "Fira Sans", sans-serif;
  text-align: center; }

body,
html,
.auth,
#root,
.auth-wrapper {
  width: 100%;
  height: 100%; }

.navbar-light {
  background-color: #ffffff;
  box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2); }

.auth-wrapper {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: left; }

.auth-inner {
  width: 450px;
  margin: auto;
  background: #ffffff;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  padding: 40px 55px 45px 55px;
  border-radius: 15px;
  -webkit-transition: all 0.3s;
  transition: all 0.3s; }

.auth-wrapper .form-control:focus {
  border-color: #ffa31a;
  box-shadow: none; }

.auth-wrapper h3 {
  text-align: center;
  margin: 0;
  line-height: 1;
  padding-bottom: 20px; }

.custom-control-label {
  font-weight: 400; }

.forgot-password,
.forgot-password a {
  text-align: right;
  font-size: 14px;
  font-style: italic;
  padding-top: 10px;
  color: #7f7d7d;
  margin: 0; }

.forgot-password a {
  color: #ffa31a; }

.forgot-password a:hover {
  color: #ffa31a; }

@media (max-width: 560px) {
  .auth-inner {
    width: 90vw; } }

